import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchIcons,fetchStyle,changeCategories } from "../../actions/iconActions";
import { bindActionCreators } from "redux";
import IconsList from "./iconsList";
import styled from 'styled-components';
import dynamic from 'next/dynamic';
import { BREAKPOINT_LG,FILTER_COLOR,TRANSITION,BREAKPOINT_MDS,BREAKPOINT_MDL,BUTTON_BOX_SHADOW,ACTIVE_NAV_LIST_COLOR,BOX_SHADOW_POPUP,BUTTON_RADIUS,SIDE_BOX_SHADOW,SIDE_WIDTH,RED_COLOR,ICONS_DARK_BACKGROUND,NAV_LIST_COLOR,SELECTED_ACTIVE_COLOR,FILTER_BACKGROUND,PADDING,BUTTON_COLOR,DARK_BACKGROUND,SELECTED_COLOR,FADE_FONT_COLOR,HEADER_COLOR,FLEX_ACTIVE,ICONS_BACKGROUND,BLUE_COLOR,LIGHT_BACKGROUND,MAX_WIDTH,DARK_BORDER_COLOR,BORDER_COLOR,SECONDARY_FONT_COLOR,PRIMARY_COLOR,FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM, DARK_FILTER_BACKGROUND, BORDER_COLOR_DARK, DARK_FONT_COLOR, LIGHT_BLUE_COLOR, SEARCH_RADIUS} from '../types';
import {LoadButton} from '../common/buttons';
